export default {

	props: [
		'channel',
		'initial',
		'initial-notif',
		'show-notif'
	],

	data() {
		return {
			loading: false,
			error: false,
			following: this.initial,
			notifications: this.initialNotif
		}
	},

	created() {
		this.twitch_data = FrankerFaceZ.get().resolve('site.twitch_data');

		if ( this.following == null || (this.notifications == null && this.showNotif) )
			this.checkFollowing();
	},

	methods: {
		async checkFollowing() {
			if ( this.loading )
				return;

			if ( ! this.twitch_data ) {
				this.error = true;
				return;
			}

			this.loading = true;

			let following;
			try {
				following = await this.twitch_data.getUserFollowed(this.channel);
			} catch(err) {
				console.error(err);
				this.error = true;
				return;
			}

			this.loading = false;
			this.following = !! following?.followedAt;
			this.notifications = !! following.disableNotifications;
		},

		async followUser() {
			if ( this.loading )
				return;

			if ( ! this.twitch_data ) {
				this.error = true;
				return;
			}

			this.loading = true;

			let following;
			try {
				following = await this.twitch_data.followUser(this.channel);
			} catch(err) {
				console.error(err);
				this.error = true;
				return;
			}

			this.loading = false;
			this.following = !! following?.followedAt;
			this.notifications = !! following.disableNotifications;
		},

		async unfollowUser() {
			if ( this.loading )
				return;

			if ( ! this.twitch_data ) {
				this.error = true;
				return;
			}

			this.loading = true;

			try {
				await this.twitch_data.unfollowUser(this.channel);
			} catch(err) {
				console.error(err);
				this.error = true;
				return;
			}

			this.loading = false;
			this.following = false;
		}

	}

}

