export default {
	props: {
		color: {
			type: String,
			default: 'background-base'
		},

		size: String,
		dir: String
	},

	computed: {
		classes() {
			let dir = '';
			if ( this.dir ) {
				dir = this.dir.split('-').map(d => {
					if ( d === 'up' || d === 'down' )
						return `ffz-il-tooltip--${d}`;
					if ( d === 'left' || d === 'right' )
						return `ffz-il-tooltip--align-${d}`;
					return '';
				}).join(' ');
			}

			return `tw-c-${this.color} ${this.size ? `ffz-balloon--${this.size}` : ''} ${dir}`;
		}
	}
}

