import { debounce } from 'utilities/object';

let id = 0;

function readEmoteMenuEmotes(input, out, seen) {
	if ( Array.isArray(input) ) {
		for(const item of input)
			readEmoteMenuEmotes(item, out, seen);
		return;
	}

	if ( ! Array.isArray(input?.emotes) )
		return;

	for(const emote of input.emotes) {
		if ( emote.locked || seen.has(emote.name) )
			continue;

		seen.add(emote.name);

		out.push({
			provider: emote.provider,
			id: emote.id,
			set_id: emote.set_id,
			name: emote.name,
			lname: emote.name && emote.name.toLowerCase(),
			src: emote.src
		});
	}
}

export default {
	props: {
		value: Object,
		alwaysOpen: {
			type: Boolean,
			required: false,
			default: false
		},
		clearable: {
			type: Boolean,
			required: false,
			default: false
		},
		direction: {
			type: String,
			required: false,
			default: 'down'
		}
	},

	data() {
		return {
			id: id++,
			open: false,
			val: this.value,
			search: '',
			emotes: []
		}
	},

	computed: {
		visible() {
			if ( ! this.search || ! this.search.length )
				return this.emotes;

			const search = this.search.toLowerCase();
			return this.emotes.filter(x => x.lname && x.lname.indexOf(search) !== -1);
		},

		valName() {
			return this.val?.name;
		},

		isOpen() {
			return this.alwaysOpen || this.open
		}
	},

	watch: {
		value() {
			this.val = this.value;
		},

		isOpen() {
			if ( ! this.isOpen ) {
				requestAnimationFrame(() => {
					const ffz = FrankerFaceZ.get();
					if ( ffz )
						ffz.emit('tooltips:cleanup');
				});
				return;
			}

			this.maybeLoadEmotes();

			this.$nextTick(() => {
				if ( this.val ) {
					const root = this.$refs.list,
						el = root && root.querySelector('.ffz-interactable--selected');

					if ( el )
						el.scrollIntoViewIfNeeded();
				}
			});
		}
	},

	created() {
		this.maybeClose = debounce(this.maybeClose, 10);
	},

	methods: {
		maybeLoadEmotes() {
			if ( ! this.emotes || ! this.emotes.length ) {
				const emotes = [],
					seen = new Set,
					menu = window.ffz_menu,
					state = menu?.state;

				if ( menu ) {
					menu.loadData();
					readEmoteMenuEmotes(state?.channel_sets, emotes, seen);
					readEmoteMenuEmotes(state?.all_sets, emotes, seen);
				}

				this.emotes = emotes;
			}
		},

		update() {
			if ( this.isOpen )
				this.search = this.$refs.input.value;
		},

		close() {
			this.open = false;
		},

		change(val, close = true) {
			this.val = {
				type: 'emote',
				provider: val.provider,
				id: val.id,
				name: val.name,
				src: val.src
			},
			this.$emit('input', this.val);
			if ( close )
				this.open = false;
		},

		onFocus(open = true) {
			this.focused = true;
			if ( open )
				this.open = true;
		},

		onBlur() {
			this.focused = false;
			this.maybeClose();
		},

		maybeClose() {
			if ( ! this.focused )
				this.open = false;
		}
	}
}

