import {Color} from 'utilities/color';

import {Sketch} from 'vue-color';

export default {
	components: {
		'chrome-picker': Sketch
	},

	props: {
		value: String,
		alpha: {
			type: Boolean,
			default: true
		},
		disabled: {
			type: Boolean,
			default: false
		},
		default: {
			type: String,
			default: '#000'
		},
		nullable: {
			type: Boolean,
			default: false
		},
		validate: {
			type: Boolean,
			default: true
		},
		showInput: {
			type: Boolean,
			default: true
		},
		tooltip: {
			type: String,
			default: null
		},
		openUp: {
			type: Boolean,
			default: false
		}
	},

	data() {
		return {
			color: '',
			valid: false,
			open: false
		}
	},

	computed: {
		hasTooltip() {
			return this.tooltip?.length > 0 || this.nullable
		},

		colors() {
			try {
				return Color.RGBA.fromCSS(this.color || this.default)
			} catch(err) {
				return {
					hex: this.default
				}
			}
		}
	},

	watch: {
		value(val) {
			this.color = val;
		}
	},

	mounted() {
		this.color = this.value;
		this._validate();
	},

	methods: {
		maybeResetColor() {
			if ( this.open )
				return this.open = false;

			if ( this.nullable ) {
				this.color = '';
				this._validate();
				this.emit();
			}
		},

		openPicker() {
			this.open = true;
		},

		closePicker() {
			this.open = false;
		},

		togglePicker() {
			this.open = ! this.open;
		},

		onPick(color) {
			if ( this.disabled )
				return;

			const old_val = this.color;

			if ( color.rgba.a == 1 )
				this.color = color.hex;
			else {
				const c = color.rgba;
				this.color = `rgba(${c.r}, ${c.g}, ${c.b}, ${c.a})`;
			}

			if ( old_val !== this.color ) {
				this._validate();
				this.emit();
			}
		},

		_validate() {
			this.valid = true;
			if ( ! this.color || ! this.color.length || ! this.validate )
				return;

			try {
				Color.RGBA.fromCSS(this.color);
			} catch(err) {
				this.valid = false;
			}
		},

		emit() {
			if ( this.valid )
				this.$emit('input', this.color);
		},

		onChange() {
			this._validate();
			this.emit();
		}
	}
}

