import {KEYS} from 'utilities/constants';

const IGNORE_KEYS = [
	KEYS.Shift,
	KEYS.Control,
	KEYS.Alt,
	KEYS.Meta
];

const BAD_KEYS = [
	KEYS.Escape,
	KEYS.Tab
];

const KEYS_MAP = {
	Backquote: '`',
	Comma: ',',
	Period: '.',
	Slash: '/',
	Semicolon: ';',
	Quote: "'",
	BracketLeft: '[',
	BracketRight: ']',
	Backslash: '\\',
	Minus: '-',
	Equal: '=',
	ArrowLeft: 'left',
	ArrowRight: 'right',
	ArrowUp: 'up',
	ArrowDown: 'down'
};

for(const num of [1,2,3,4,5,6,7,8,9,0])
	KEYS_MAP[`Digit${num}`] = `${num}`;

for(const letter of 'abcdefghijklmnopqrstuvwxyz')
	KEYS_MAP[`Key${letter.toUpperCase()}`] = letter;


export default {
	props: {
		value: String,
		disabled: {
			type: Boolean,
			default: false
		}
	},

	data() {
		return {
			active: false
		}
	},

	methods: {
		onKey(e) {
			if ( this.active )
				this.record(e);
		},

		onKeyUp(e) {
			if ( this.active )
				this.finishRecording(e);
			else {
				const key = e.keyCode || e.which;
				if ( key === KEYS.Enter || key === KEYS.Space )
					this.startRecording(e);
			}
		},

		stop(e) {
			e.preventDefault();
			e.stopImmediatePropagation();
			e.stopPropagation();
		},

		startRecording(e) {
			if ( e )
				this.stop(e);

			if ( this.disabled )
				return;

			if ( this.active )
				return;

			this.active = true;
			this.key = null;
		},

		record(e) {
			const key = e.keyCode || e.which;
			if ( BAD_KEYS.includes(key) )
				return;

			this.stop(e);

			if ( IGNORE_KEYS.includes(key) )
				return;

			this.key = e;
		},

		cancelRecording() {
			this.active = false;
		},

		finishRecording(e) {
			const k = e.keyCode || e.which;
			if ( BAD_KEYS.includes(k) )
				return;

			this.stop(e);

			if ( this.disabled )
				return;

			if ( IGNORE_KEYS.includes(k) )
				return;

			this.active = false;
			const key = this.key;
			this.key = null;
			if ( ! key )
				return;

			let code = KEYS_MAP[key.code];
			if ( ! code )
				code = key.key;

			const val = `${key.ctrlKey ? 'ctrl+' : ''}${key.altKey ? 'alt+' : ''}${key.shiftKey ? 'shift+' : ''}${code}`;
			this.$emit('input', val);
		},

		clear() {
			if ( this.disabled )
				return;

			this.$emit('input', null);
		}
	}

}

